import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Figure from 'react-bootstrap/Figure';
import { Link } from "react-router-dom";
import './aboutUs.scss'
import ab1 from './ab1.png'
import ab2 from './ab2.png'
import ab3 from './ab3.png'
import how1 from './how1.png'
import how2 from './how2.png'
import how3 from './how3.png'
function AboutUs(props) {
    return (
        <div>
            <Container>
            <Row>
                <Col className='about-title'>  ?</Col>
            </Row>
            <Row>
                <Col>
                    <Figure style={{height:'280px'}}>
                        <Figure.Image
                            width={161}
                            height={170}
                            alt="171x180"
                            src={ab1}
                        />
                        <Figure.Caption className='about-title-container'></Figure.Caption>
                        <Figure.Caption className='about-text'>   </Figure.Caption>
                        
                    </Figure>
                    <Link to="/information" relative="path">
                    <Button variant="outline-warning" style={{marginTop:'2%'}}></Button>
                    </Link>
                </Col>
                <Col>
                <Figure style={{height:'280px'}}>
                        <Figure.Image
                            width={161}
                            height={170}
                            alt="171x180"
                            src={ab2}
                        />
                        <Figure.Caption className='about-title-container'> </Figure.Caption>
                        <Figure.Caption className='about-text'>      ' </Figure.Caption>
                    </Figure>
                    <Link to="/search" relative="path">
                    <Button variant="outline-warning"  style={{marginTop:'2%'}}></Button>
                    </Link>
                </Col>
                <Col>
                <Figure style={{height:'280px'}}>
                        <Figure.Image
                            width={161}
                            height={170}
                            alt="171x180"
                            src={ab3}
                        />
                        <Figure.Caption className='about-title-container'></Figure.Caption>
                        <Figure.Caption className='about-text'>         </Figure.Caption>
                    </Figure>
                    <Link to="/forum" relative="path">
                    <Button variant="outline-warning"  style={{marginTop:'2%'}}></Button>
                    </Link>
                </Col>
            </Row>

            <Row>
                <Col className='about-title'> ?</Col>
            </Row>
            <Row>
                <Col>
                    <Figure>
                        <Figure.Image
                            width={300}
                            height={400}
                            alt="171x180"
                            src={how1}
                        />
                        {/* <Figure.Caption className='about-title-container'></Figure.Caption> */}
                        <Figure.Caption className='about-text-how'>      </Figure.Caption>
                    </Figure>
                    <Link to="/register" relative="path">
                    <Button variant="outline-warning"  style={{marginTop:'2%'}}></Button>
                    </Link>
                </Col>
                <Col>
                <Figure>
                        <Figure.Image
                            width={300}
                            height={400}
                            alt="171x180"
                            src={how2}
                        />
                        {/* <Figure.Caption className='about-title-container'></Figure.Caption> */}
                        <Figure.Caption className='about-text-how'> ,        </Figure.Caption>
                    </Figure>
                    <Link to="/forum" relative="path">
                    <Button variant="outline-warning"  style={{marginTop:'2%'}}> </Button>
                    </Link>
                </Col>
                <Col>
                <Figure>
                        <Figure.Image
                            width={300}
                            height={400}
                            alt="171x180"
                            src={how3}
                        />
                        {/* <Figure.Caption className='about-title-container'></Figure.Caption> */}
                        <Figure.Caption className='about-text-how'> '    </Figure.Caption>
                    </Figure>
                </Col>
            </Row>
            </Container>
        </div>
    );
}

export default AboutUs;